import * as React from "react";
import type { HeadFC } from "gatsby";
import { Link, navigate } from "gatsby";
import AppContext from "../../context/AppContext";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Dropdown from "react-bootstrap/Dropdown";
import { WrappedParse as Parse } from "../../vendor/ParseApp";
import Spinner from "react-bootstrap/Spinner";
import {
  Trans,
  useTranslation,
  I18nextContext,
} from "gatsby-plugin-react-i18next";
import Help from "../help";

import { PUBLIC_ROUTES } from "../../constant";

const CustomToggle = React.forwardRef(({ children, onClick }, ref) => (
  <a
    href=""
    ref={ref}
    onClick={(e) => {
      e.preventDefault();
      onClick(e);
    }}
  >
    {children}
    &#x25bc;
  </a>
));

const IndexPage = ({ children }) => {
  const { useEffect, useState } = React;
  const [roles, setRoles] = useState([]);

  const { t } = useTranslation();
  const [isLoading, setIsLoading] = React.useState(false);
  const [contextState, setContextState] = React.useState({
    theme: "",
    showLoading: (show) => {
      setIsLoading(show);
    },
  });
  const user = Parse?.User?.current();
  const isAuthenticated = !!user;

  useEffect(() => {
    const path = typeof window !== "undefined" ? window.location.pathname : "";
    if (PUBLIC_ROUTES.includes(path) && isAuthenticated) {
      console.log(path);
      navigate("/");
    }
    Parse.Cloud.run("getMyRole").then((e) => {
      setRoles(e);
    });
  }, []);
  return (
    <AppContext.Provider value={contextState}>
      <Help />
      <main>
        <div className="navbar navbar-expand-lg navbar-light bg-brand-1 d-flex mb-5">
          <Container>
            <div className="d-flex flex-row">
              <div className="circle-avatar me-2 defaultCursor">
                <span className="initials">{"JC"}</span>
              </div>
              <div className="d-flex justify-content-center  align-items-center">
                <h3>
                  <Link to="/">Jarvis</Link>
                </h3>
              </div>
            </div>
            <div>
              {"" && (
                <input
                  type="text"
                  className="form-control bg-brand-5 color-brand-6"
                  id="exampleFormControlInput1"
                  placeholder="Search In Web3"
                ></input>
              )}
            </div>

            <div className="text-uppercase d-flex flex-direction-row gap-6 color-white gap-3">
              {}

              {isAuthenticated ? (
                <Dropdown>
                  <Dropdown.Toggle
                    className="color-brand1"
                    as={CustomToggle}
                    variant="success"
                    id="dropdown-basic"
                  >
                    {user.get("email")}
                  </Dropdown.Toggle>

                  <Dropdown.Menu>
                  {!roles.includes("TA") && <Dropdown.Item as="div">
                      {" "}
                      <Link className="color-brand-1" to="/start">
                        {t("myapplication")}
                      </Link>
                    </Dropdown.Item>
                    }

                    {roles.includes("TA") && (
                      <Dropdown.Item as="div">
                        {" "}
                        <Link className="color-brand-1" to="/search">
                          {t("searchapplication")}
                        </Link>
                      </Dropdown.Item>
                    )}
                    <Dropdown.Item as="div">
                      {" "}
                      <Link className="color-brand-1" to="/account">
                        {t("myprofile")}
                      </Link>
                    </Dropdown.Item>

                    <Dropdown.Item
                      as="div"
                      onClick={() =>
                        Parse?.User.logOut().then((e) => navigate("/"))
                      }
                    >
                      {t("logout")}
                    </Dropdown.Item>
                    <Dropdown.Item as="div">
                        {" "}
                        <Link className="color-brand-1" to="/help">
                          {t("help")}
                        </Link>
                      </Dropdown.Item>
                  </Dropdown.Menu>
                </Dropdown>
              ) : (
                <div className="defaultCursor">
                  <Link to="/login"> { t('login') }</Link> |
                  <Link to="/signup"> { t('signup') } </Link>
                  |
                  <Link to="/help"> { t('help') }</Link>
                </div>
              )}
            </div>
          </Container>
        </div>

        <Container fluid className="position-relative page">
          <Row>
            <Col xs={12} extraClass="col-page">
              {children}
              {isLoading && (
                <div class="spotlight">
                  <Spinner animation="border" role="status">
                    <span className="visually-hidden">Loading...</span>
                  </Spinner>
                </div>
              )}
            </Col>
          </Row>
        </Container>
      </main>
    </AppContext.Provider>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>Home Page</title>;
